<template>
<div class="section">
<h1>Épisodes de pollution prévus à {{ nomVille }}</h1>
</div>
</template>
<script>
export default {
    name: 'Episodes',
    data: function() {
        return {
            "episodes": [],
            "nomVille": ""
        }
    },
    props: ['insee'],
    created: function() {
        console.log('episodes created')
        this.fetchData()
    },
    methods: {
        fetchData() {
            fetch(`https://geo.api.gouv.fr/communes/${this.insee}?fields=nom&format=json&geometry=centre`)
                .then(response => response.json())
                .then(data => {
                    this.nomVille = data['nom']
                })

            fetch(`/episodes?insee=${this.insee}`)
            .then(response => response.json())
            .then(data => {
                this.episodes = data.data
                console.log(this.episodes)
            })
        }
    }
}
</script>